/*** Text Utilities ***/
.list-item {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding: 5px 35px;
  background: #FBFCFC;
  cursor: pointer;
  border: 1px solid #5F6B77;
  border-radius: 10px;
  transition: background 0.3s;
  font-family: sans-serif; }
  .list-item:hover {
    background: #D2F2FF; }

.list-item__title {
  margin-right: 30px; }
