/*** Text Utilities ***/
.ecl-btn {
  font-family: grotesk;
  font-weight: 700;
  font-size: 0.875em;
  letter-spacing: 0.25px;
  margin: initial;
  padding: 10px 21px;
  border-radius: 25px;
  cursor: pointer;
  font-family: sans-serif; }
  .ecl-btn.primary {
    background: #6ED2FA;
    color: #FFFFFF;
    -moz-transition: background 0.2s;
    -webkit-transition: background 0.2s;
    transition: background 0.2s; }
    .ecl-btn.primary:hover {
      background: #D2F2FF;
      outline: none; }
    .ecl-btn.primary:active, .ecl-btn.primary:focus {
      outline: none;
      background: #F2F8FC; }
    .ecl-btn.primary.disabled {
      background: #DEE1E5;
      cursor: not-allowed;
      pointer-events: none; }

.btn-unstyled {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer; }
  .btn-unstyled:focus {
    outline: none; }
