@font-face {
  font-family: grotesk;
  font-weight: 400;
  src: url("/assets/fonts/grotesk/HKGrotesk-Regular.otf") format("opentype"); }

@font-face {
  font-family: grotesk;
  font-weight: 500;
  src: url("/assets/fonts/grotesk/HKGrotesk-Medium.otf") format("opentype"); }

@font-face {
  font-family: grotesk;
  font-weight: 700;
  src: url("/assets/fonts/grotesk/HKGrotesk-Bold.otf") format("opentype"); }

@font-face {
  font-family: grotesk;
  font-weight: 300;
  src: url("/assets/fonts/grotesk/HKGrotesk-Light.otf") format("opentype"); }

/*** Text Utilities ***/
.prop-table {
  background: red; }
  .prop-table table {
    width: 100%; }
  .prop-table th {
    font-family: grotesk;
    font-weight: 700;
    font-size: 1.125rem;
    letter-spacing: 0.25px;
    margin: initial;
    padding: 5px 10px; }
    @media (max-width: 639px) {
      .prop-table th {
        font-size: 1rem;
        letter-spacing: 0; } }
  .prop-table td {
    font-family: grotesk;
    font-weight: 300;
    font-size: 0.875rem;
    letter-spacing: 0.25px;
    margin: initial;
    padding: 5px 10px; }
  .prop-table table, .prop-table th, .prop-table td {
    border: 1px solid #ABB5BE;
    border-collapse: collapse; }
  .prop-table tr:nth-child(even) {
    background: #FAFAFA; }
  .prop-table .story-link {
    font-family: grotesk;
    font-weight: 700;
    font-size: 0.875em;
    letter-spacing: 0.25px;
    margin: initial;
    cursor: pointer;
    color: #6ED2FA; }
    .prop-table .story-link:hover {
      text-decoration: underline; }
