@font-face {
  font-family: grotesk;
  font-weight: 400;
  src: url("/assets/fonts/grotesk/HKGrotesk-Regular.otf") format("opentype"); }

@font-face {
  font-family: grotesk;
  font-weight: 500;
  src: url("/assets/fonts/grotesk/HKGrotesk-Medium.otf") format("opentype"); }

@font-face {
  font-family: grotesk;
  font-weight: 700;
  src: url("/assets/fonts/grotesk/HKGrotesk-Bold.otf") format("opentype"); }

